import React, { useEffect, useState, useCallback } from "react";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import Card from "@mui/material/Card";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDInput from "components/MDInput";
import MDButton from "components/MDButton";
import { VhoozhtInput, VhoozhtSelect, VhoozhtTextarea } from "components/VhoozhtForms";
import EnhancedTable from "components/EnhancedTable";
import { formatters } from "utils/dataFormatters";
import adminDataService from "../../services/adminDataService";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
// import TextField from "@mui/material/TextField"; // Replaced with VhoozhtInput
import Checkbox from "@mui/material/Checkbox";
// import Select from "@mui/material/Select"; // Replaced with VhoozhtSelect
// import MenuItem from "@mui/material/MenuItem"; // Replaced with VhoozhtSelect options
// import FormControl from "@mui/material/FormControl"; // Replaced with VhoozhtSelect
// import InputLabel from "@mui/material/InputLabel"; // Replaced with VhoozhtSelect labels
import Grid from "@mui/material/Grid";
import Chip from "@mui/material/Chip";
import IconButton from "@mui/material/IconButton";
import Icon from "@mui/material/Icon";
import Tooltip from "@mui/material/Tooltip";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";
import TableContainer from "@mui/material/TableContainer";
import Table from "@mui/material/Table";
import TableHead from "@mui/material/TableHead";
import TableBody from "@mui/material/TableBody";
import TableRow from "@mui/material/TableRow";
import TableCell from "@mui/material/TableCell";
import Pagination from "@mui/material/Pagination";
import { alpha } from "@mui/material/styles";

// Loan Edit Modal
function LoanEditModal({ open, onClose, loan, onSave }) {
  const [form, setForm] = useState({
    amount: 0,
    interestRate: 0,
    term: 0,
    status: "",
    remainingBalance: 0,
    monthlyPayment: 0
  });

  useEffect(() => {
    if (loan) {
      setForm({
        amount: loan.amount || 0,
        interestRate: loan.interestRate || 0,
        term: loan.term || 0,
        status: loan.status || "",
        remainingBalance: loan.remainingBalance || 0,
        monthlyPayment: loan.monthlyPayment || 0,
      });
    }
  }, [loan]);

  if (!loan) return null;

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth="md">
      <DialogTitle>Edit Loan: {loan.id}</DialogTitle>
      <DialogContent>
        <Grid container spacing={2} sx={{ mt: 1 }}>
          <Grid item xs={12} md={6}>
            <VhoozhtInput
              label="Loan Amount ($)"
              fullWidth
              type="number"
              value={form.amount}
              onChange={e => setForm(f => ({ ...f, amount: parseFloat(e.target.value) || 0 }))}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <VhoozhtInput
              label="Interest Rate (%)"
              fullWidth
              type="number"
              step="0.01"
              value={form.interestRate}
              onChange={e => setForm(f => ({ ...f, interestRate: parseFloat(e.target.value) || 0 }))}
            />
          </Grid>
          <Grid item xs={12} md={4}>
            <VhoozhtInput
              label="Term (months)"
              fullWidth
              type="number"
              value={form.term}
              onChange={e => setForm(f => ({ ...f, term: parseInt(e.target.value) || 0 }))}
            />
          </Grid>
          <Grid item xs={12} md={4}>
            <VhoozhtSelect
              label="Status"
              fullWidth
              value={form.status}
              onChange={e => setForm(f => ({ ...f, status: e.target.value }))}
              options={[
                { value: 'pending', label: 'Pending' },
                { value: 'approved', label: 'Approved' },
                { value: 'denied', label: 'Denied' },
                { value: 'active', label: 'Active' },
                { value: 'paid', label: 'Paid' },
                { value: 'defaulted', label: 'Defaulted' }
              ]}
            />
          </Grid>
          <Grid item xs={12} md={4}>
            <VhoozhtInput
              label="Monthly Extension ($)"
              fullWidth
              type="number"
              value={form.monthlyPayment}
              onChange={e => setForm(f => ({ ...f, monthlyPayment: parseFloat(e.target.value) || 0 }))}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <VhoozhtInput
              label="Remaining Balance ($)"
              fullWidth
              type="number"
              value={form.remainingBalance}
              onChange={e => setForm(f => ({ ...f, remainingBalance: parseFloat(e.target.value) || 0 }))}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <MDButton variant="outlined" onClick={onClose}>Cancel</MDButton>
        <MDButton variant="contained" onClick={() => onSave({ ...loan, ...form })}>Save Changes</MDButton>
      </DialogActions>
    </Dialog>
  );
}

// Bulk Loan Operations Modal
function BulkLoanOperationsModal({ open, onClose, selectedLoans, onBulkAction }) {
  const [operation, setOperation] = useState("");
  const [newStatus, setNewStatus] = useState("");
  const [interestRateAdjustment, setInterestRateAdjustment] = useState(0);

  const handleBulkAction = () => {
    const loanIds = selectedLoans.map(l => l.id);
    
    switch (operation) {
      case 'approve':
        onBulkAction('approve', loanIds);
        break;
      case 'deny':
        onBulkAction('deny', loanIds);
        break;
      case 'updateStatus':
        if (newStatus) {
          onBulkAction('status', loanIds, { status: newStatus });
        }
        break;
      case 'adjustRate':
        if (interestRateAdjustment !== 0) {
          onBulkAction('rate', loanIds, { rateAdjustment: interestRateAdjustment });
        }
        break;
      case 'delete':
        if (window.confirm(`Are you sure you want to delete ${loanIds.length} loans? This action cannot be undone.`)) {
          onBulkAction('delete', loanIds);
        }
        break;
    }
    onClose();
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>Bulk Loan Operations ({selectedLoans.length} loans selected)</DialogTitle>
      <DialogContent>
        <Grid container spacing={2} sx={{ mt: 1 }}>
          <Grid item xs={12}>
            <VhoozhtSelect
              label="Operation"
              fullWidth
              value={operation}
              onChange={e => setOperation(e.target.value)}
              options={[
                { value: 'approve', label: 'Approve All' },
                { value: 'deny', label: 'Deny All' },
                { value: 'updateStatus', label: 'Update Status' },
                { value: 'adjustRate', label: 'Adjust Interest Rate' },
                { value: 'delete', label: 'Delete All' }
              ]}
            />
          </Grid>
          
          {operation === 'updateStatus' && (
            <Grid item xs={12}>
              <VhoozhtSelect
                label="New Status"
                fullWidth
                value={newStatus}
                onChange={e => setNewStatus(e.target.value)}
                options={[
                  { value: 'pending', label: 'Pending' },
                  { value: 'approved', label: 'Approved' },
                  { value: 'denied', label: 'Denied' },
                  { value: 'active', label: 'Active' },
                  { value: 'paid', label: 'Paid' },
                  { value: 'defaulted', label: 'Defaulted' }
                ]}
              />
            </Grid>
          )}
          
          {operation === 'adjustRate' && (
            <Grid item xs={12}>
              <VhoozhtInput
                fullWidth
                label="Interest Rate Adjustment (%)"
                type="number"
                step="0.01"
                value={interestRateAdjustment}
                onChange={e => setInterestRateAdjustment(parseFloat(e.target.value) || 0)}
                helperText="Positive number to increase, negative to decrease"
              />
            </Grid>
          )}
        </Grid>
      </DialogContent>
      <DialogActions>
        <MDButton variant="outlined" onClick={onClose}>Cancel</MDButton>
        <MDButton variant="contained" onClick={handleBulkAction}>Apply</MDButton>
      </DialogActions>
    </Dialog>
  );
}

function AdminLoans() {
  const [loans, setLoans] = useState([]);
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);
  
  // Search and filters
  const [search, setSearch] = useState("");
  const [statusFilter, setStatusFilter] = useState("");
  const [dateFromFilter, setDateFromFilter] = useState("");
  const [dateToFilter, setDateToFilter] = useState("");
  const [amountMinFilter, setAmountMinFilter] = useState("");
  const [amountMaxFilter, setAmountMaxFilter] = useState("");
  
  // Pagination and sorting
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [sortBy, setSortBy] = useState("applicationDate");
  const [sortOrder, setSortOrder] = useState("desc");
  
  // Selection and modals
  const [selectedLoans, setSelectedLoans] = useState([]);
  const [selectAll, setSelectAll] = useState(false);
  const [editing, setEditing] = useState(null);
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [bulkModalOpen, setBulkModalOpen] = useState(false);
  
  // Notifications
  const [notification, setNotification] = useState({ open: false, message: "", severity: "success" });

  const showNotification = (message, severity = "success") => {
    setNotification({ open: true, message, severity });
  };

  // Fetch data with filters
  const fetchLoans = useCallback(async () => {
    setLoading(true);
    try {
      const filters = {
        page,
        pageSize,
        search,
        status: statusFilter,
        dateFrom: dateFromFilter,
        dateTo: dateToFilter,
        amountMin: amountMinFilter ? parseFloat(amountMinFilter) : undefined,
        amountMax: amountMaxFilter ? parseFloat(amountMaxFilter) : undefined,
        sortBy,
        sortOrder,
      };

      const result = adminDataService.getLoans(filters);
      setLoans(result.items);
      setTotal(result.total);
    } catch (error) {
      showNotification("Error loading loans", "error");
    } finally {
      setLoading(false);
    }
  }, [page, pageSize, search, statusFilter, dateFromFilter, dateToFilter, amountMinFilter, amountMaxFilter, sortBy, sortOrder]);

  useEffect(() => {
    fetchLoans();
  }, [fetchLoans]);

  // Selection handlers
  const handleSelectAll = (checked) => {
    setSelectAll(checked);
    setSelectedLoans(checked ? [...loans] : []);
  };

  const handleSelectLoan = (loan, checked) => {
    if (checked) {
      setSelectedLoans([...selectedLoans, loan]);
    } else {
      setSelectedLoans(selectedLoans.filter(l => l.id !== loan.id));
      setSelectAll(false);
    }
  };

  // CRUD operations
  const handleSaveLoan = async (updatedLoan) => {
    try {
      const result = adminDataService.updateLoan(updatedLoan.id, updatedLoan);
      if (result) {
        showNotification("Loan updated successfully");
        setEditModalOpen(false);
        fetchLoans();
      }
    } catch (error) {
      showNotification("Error updating loan", "error");
    }
  };

  const handleBulkAction = async (actionType, loanIds, params = {}) => {
    try {
      let result;
      
      switch (actionType) {
        case 'approve':
          result = adminDataService.approveLoans(loanIds);
          showNotification(`${result.approvedCount} loans approved`);
          break;
        case 'deny':
          result = adminDataService.denyLoans(loanIds);
          showNotification(`${result.deniedCount} loans denied`);
          break;
        case 'status':
          result = adminDataService.bulkUpdateLoans(loanIds, { status: params.status });
          showNotification(`${result.updatedCount} loans updated`);
          break;
        case 'rate':
          result = adminDataService.bulkUpdateLoans(loanIds, params);
          showNotification(`${result.updatedCount} loans updated`);
          break;
        case 'delete':
          result = adminDataService.bulkDeleteLoans(loanIds);
          showNotification(`${result.deletedCount} loans deleted`);
          break;
      }
      
      setSelectedLoans([]);
      setSelectAll(false);
      fetchLoans();
    } catch (error) {
      showNotification("Error performing bulk operation", "error");
    }
  };

  // Individual loan actions
  const handleApproveLoan = async (loanId) => {
    try {
      const result = adminDataService.approveLoans([loanId]);
      if (result.approvedCount > 0) {
        showNotification("Loan approved successfully");
        fetchLoans();
      }
    } catch (error) {
      showNotification("Error approving loan", "error");
    }
  };

  const handleDenyLoan = async (loanId) => {
    try {
      const result = adminDataService.denyLoans([loanId]);
      if (result.deniedCount > 0) {
        showNotification("Loan denied");
        fetchLoans();
      }
    } catch (error) {
      showNotification("Error denying loan", "error");
    }
  };

  // Export functions
  const handleExportCSV = () => {
    const filteredLoans = selectedLoans.length ? selectedLoans : loans;
    adminDataService.exportCSV('loans', filteredLoans);
    showNotification(`Exported ${filteredLoans.length} loans to CSV`);
  };

  const handleExportJSON = () => {
    adminDataService.exportData('loans');
    showNotification("Loans data exported to JSON");
  };

  // Clear filters
  const clearFilters = () => {
    setSearch("");
    setStatusFilter("");
    setDateFromFilter("");
    setDateToFilter("");
    setAmountMinFilter("");
    setAmountMaxFilter("");
    setPage(1);
  };

  const totalPages = Math.max(1, Math.ceil(total / pageSize));

  const getStatusColor = (status) => {
    switch (status) {
      case 'approved': return 'success';
      case 'denied': return 'error';
      case 'pending': return 'warning';
      case 'active': return 'info';
      case 'paid': return 'success';
      case 'defaulted': return 'error';
      default: return 'default';
    }
  };

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <Card>
      {/* Header */}
      <MDBox p={3}>
        <MDBox display="flex" justifyContent="space-between" alignItems="center" mb={2}>
          <MDBox>
            <MDTypography variant="h6">Loans Management</MDTypography>
            <MDTypography variant="caption" color="text">
              Total: {total} loans | Selected: {selectedLoans.length}
            </MDTypography>
          </MDBox>
          <MDBox display="flex" gap={1}>
            <MDButton variant="outlined" onClick={handleExportCSV} disabled={!loans.length}>
              Export CSV
            </MDButton>
            <MDButton variant="outlined" onClick={handleExportJSON}>
              Export JSON
            </MDButton>
            {selectedLoans.length > 0 && (
              <MDButton variant="contained" color="warning" onClick={() => setBulkModalOpen(true)}>
                Bulk Actions ({selectedLoans.length})
              </MDButton>
            )}
          </MDBox>
        </MDBox>

        {/* Filters */}
        <Grid container spacing={2} mb={2}>
          <Grid item xs={12} md={3}>
            <VhoozhtInput
              placeholder="Search loans..."
              value={search}
              onChange={(e) => { setSearch(e.target.value); setPage(1); }}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <VhoozhtSelect
              label="Status"
              size="small"
              fullWidth
              value={statusFilter}
              onChange={(e) => { setStatusFilter(e.target.value); setPage(1); }}
              options={[
                { value: '', label: 'All' },
                { value: 'pending', label: 'Pending' },
                { value: 'approved', label: 'Approved' },
                { value: 'denied', label: 'Denied' },
                { value: 'active', label: 'Active' },
                { value: 'paid', label: 'Paid' },
                { value: 'defaulted', label: 'Defaulted' }
              ]}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <VhoozhtInput
              label="Date From"
              type="date"
              fullWidth
              size="small"
              value={dateFromFilter}
              onChange={(e) => { setDateFromFilter(e.target.value); setPage(1); }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <VhoozhtInput
              label="Date To"
              type="date"
              fullWidth
              size="small"
              value={dateToFilter}
              onChange={(e) => { setDateToFilter(e.target.value); setPage(1); }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          <Grid item xs={12} md={1.5}>
            <VhoozhtInput
              label="Min Amount"
              type="number"
              fullWidth
              size="small"
              value={amountMinFilter}
              onChange={(e) => { setAmountMinFilter(e.target.value); setPage(1); }}
            />
          </Grid>
          <Grid item xs={12} md={1.5}>
            <VhoozhtInput
              label="Max Amount"
              type="number"
              fullWidth
              size="small"
              value={amountMaxFilter}
              onChange={(e) => { setAmountMaxFilter(e.target.value); setPage(1); }}
            />
          </Grid>
        </Grid>

        <MDBox display="flex" justifyContent="space-between" alignItems="center">
          <MDButton variant="outlined" onClick={clearFilters} size="small">
            Clear Filters
          </MDButton>
          <MDBox display="flex" alignItems="center" gap={1}>
            <MDTypography variant="caption">Sort by:</MDTypography>
            <VhoozhtSelect
              size="small"
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              options={[
                { value: 'applicationDate', label: 'Application Date' },
                { value: 'amount', label: 'Amount' },
                { value: 'interestRate', label: 'Interest Rate' },
                { value: 'term', label: 'Term' },
                { value: 'status', label: 'Status' }
              ]}
            />
            <VhoozhtSelect
              size="small"
              value={sortOrder}
              onChange={(e) => setSortOrder(e.target.value)}
              options={[
                { value: 'asc', label: 'Ascending' },
                { value: 'desc', label: 'Descending' }
              ]}
            />
          </MDBox>
        </MDBox>
      </MDBox>

      {/* Enhanced Table */}
      <EnhancedTable
        columns={[
          {
            id: 'id',
            label: 'Loan ID',
            width: 120,
            minWidth: 100,
            align: 'left',
            format: (value) => (
              <MDTypography variant="button" fontWeight="medium" noWrap>
                {value}
              </MDTypography>
            ),
          },
          {
            id: 'borrowerName',
            label: 'Borrower',
            width: 200,
            minWidth: 150,
            align: 'left',
            format: (value) => (
              <MDTypography variant="body2" noWrap title={value}>
                {value}
              </MDTypography>
            ),
          },
          {
            id: 'amount',
            label: 'Amount',
            width: 140,
            align: 'right',
            format: (value) => formatters.currency(value),
          },
          {
            id: 'interestRate',
            label: 'Rate',
            width: 100,
            align: 'center',
            format: (value) => formatters.percentage(value),
          },
          {
            id: 'term',
            label: 'Term',
            width: 120,
            align: 'center',
            format: (value) => `${value} months`,
          },
          {
            id: 'status',
            label: 'Status',
            width: 120,
            align: 'center',
            format: (value) => (
              <Chip
                label={value}
                color={getStatusColor(value)}
                size="small"
              />
            ),
          },
          {
            id: 'applicationDate',
            label: 'Applied',
            width: 130,
            align: 'center',
            format: (value) => formatters.date(value),
          },
          {
            id: 'actions',
            label: 'Actions',
            width: 150,
            align: 'center',
            format: (value, row) => (
              <MDBox display="flex" justifyContent="center" gap={1}>
                <Tooltip title="Edit Loan">
                  <IconButton
                    size="small"
                    onClick={() => { setEditing(row); setEditModalOpen(true); }}
                    sx={{ 
                      width: 32, 
                      height: 32,
                      '&:hover': { bgcolor: alpha('#2196f3', 0.1) }
                    }}
                  >
                    <Icon fontSize="small">edit</Icon>
                  </IconButton>
                </Tooltip>
                {row.status === 'pending' && (
                  <>
                    <Tooltip title="Approve Loan">
                      <IconButton
                        size="small"
                        onClick={() => handleApproveLoan(row.id)}
                        sx={{ 
                          width: 32, 
                          height: 32,
                          color: '#4caf50',
                          '&:hover': { bgcolor: alpha('#4caf50', 0.1) }
                        }}
                      >
                        <Icon fontSize="small">check</Icon>
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="Deny Loan">
                      <IconButton
                        size="small"
                        onClick={() => handleDenyLoan(row.id)}
                        sx={{ 
                          width: 32, 
                          height: 32,
                          color: '#f44336',
                          '&:hover': { bgcolor: alpha('#f44336', 0.1) }
                        }}
                      >
                        <Icon fontSize="small">close</Icon>
                      </IconButton>
                    </Tooltip>
                  </>
                )}
              </MDBox>
            ),
          },
        ]}
        data={loans}
        selectedItems={selectedLoans}
        onSelectAll={handleSelectAll}
        onSelectItem={handleSelectLoan}
        showCheckbox={true}
        stickyHeader={false}
        maxHeight="70vh"
      />

      {/* Pagination */}
      <MDBox p={3} display="flex" justifyContent="space-between" alignItems="center">
        <MDBox display="flex" alignItems="center" gap={2}>
          <MDTypography variant="caption">Page size:</MDTypography>
          <VhoozhtSelect
            size="small"
            value={pageSize}
            onChange={(e) => { setPageSize(Number(e.target.value)); setPage(1); }}
            options={[
              { value: 5, label: '5' },
              { value: 10, label: '10' },
              { value: 25, label: '25' },
              { value: 50, label: '50' }
            ]}
          />
        </MDBox>
        <Pagination
          count={totalPages}
          page={page}
          onChange={(e, newPage) => setPage(newPage)}
          color="primary"
        />
      </MDBox>

      {/* Modals */}
      <LoanEditModal
        open={editModalOpen}
        onClose={() => setEditModalOpen(false)}
        loan={editing}
        onSave={handleSaveLoan}
      />

      <BulkLoanOperationsModal
        open={bulkModalOpen}
        onClose={() => setBulkModalOpen(false)}
        selectedLoans={selectedLoans}
        onBulkAction={handleBulkAction}
      />

      {/* Notification */}
      <Snackbar
        open={notification.open}
        autoHideDuration={4000}
        onClose={() => setNotification({ ...notification, open: false })}
      >
        <Alert severity={notification.severity} onClose={() => setNotification({ ...notification, open: false })}>
          {notification.message}
        </Alert>
      </Snackbar>
    </Card>
    </DashboardLayout>
  );
}

export default AdminLoans;
